.Analysis {
    width: 100%;
    height: calc(100vh - 0rem);
    display: flex;
    flex-direction: column;
    padding-bottom: 2rem;
    & h2 {
        font-family: "Outfit", sans-serif;
        font-style: normal;
        font-weight: 600;
        font-size: 36px;
        line-height: 45px;
    }
    &__graphContainer {
        width: 40%;
    }
    & canvas {
        background-color: $white;
    }
}