@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@300;400;500;600;700;800&display=swap");
* {
    box-sizing: border-box;
    scroll-behavior: smooth;
    /* width */
     ::-webkit-scrollbar {
        width: 8px;
    }
    /* Track */
     ::-webkit-scrollbar-track {
        // box-shadow: inset 0 0 5px grey;
        border-radius: 10px;
    }
    /* Handle */
     ::-webkit-scrollbar-thumb {
        background: $orange;
        border-radius: 8px;
    }
}

body {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    background-color: $black;
    min-height: 100vh;
    color: $white;
    font-family: "Outfit", sans-serif;
}

.container {
    &--full {
        width: 100%;
    }
    &--content {
        width: 90%;
    }
}

.page {
    padding-top: 6.5rem;
}

.Button {
    padding: 0.5rem 1rem;
    border-radius: 4px;
    cursor: pointer;
    &--primary {
        @extend .Button;
        background-color: $white;
        color: $black;
        &:hover {
            opacity: 0.8;
            &::after {
                content: none !important;
            }
        }
    }
}

.arrowLink {
    text-decoration: none;
    color: $white;
    cursor: pointer;
    position: relative;
    transition: all 0.2s ease-in-out;
    & .arrow {
        font-size: 1.2rem;
        transition: all 0.2s ease-in-out;
        position: absolute;
        right: -1rem;
        top: -0.15rem;
    }
    &:hover {
        & .arrow {
            transition: all 0.2s ease-in-out;
            position: absolute;
            &:nth-child(2) {
                right: -1.6rem;
            }
            &:nth-child(3) {
                right: -2.2rem;
            }
        }
    }
}