.BoardsListing {
    * {
        user-select: none;
    }
    width: 100%;
    display: flex;
    height: 100vh;
    padding: 5% 0%;
    padding-top: 10%;
    justify-content: space-between;
    margin-bottom: 5rem;
    &__card {
        height: 100%;
        display: flex;
        flex-direction: column;
        // align-items: center;
        // padding: 0rem 1rem;
        & .imageHolder {
            position: relative;
            cursor: pointer;
            width: 100%;
            // background-color: $violet;
            min-height: 280px;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 1rem;
            transition: all 0.3s ease-in-out;
            &::after {
                content: "";
                position: absolute;
                // width: 50px;
                // height: 50px;
                width: 60%;
                height: 20%;
                border-radius: 50%;
                transition: all 0.3s ease-in-out;
                // background-color: white;
                box-shadow: 0px 0px 200px 50px #000;
            }
            &:hover {
                transform: scale(1.05);
                &::after {
                    // background-color: white;
                    box-shadow: 0px 0px 150px 20px $orange;
                }
            }
            img {
                z-index: 1;
                position: absolute;
                width: 100%;
                margin-top: 1%;
                // transition: all 1s ease-in-out;
                animation: floatingObject 2s linear infinite;
                &:hover {
                    animation: none;
                }
            }
        }
        >span {
            width: 100%;
            text-align: center;
            font-size: 1.5rem;
            text-decoration: underline;
        }
        .links {
            width: 100%;
            margin-top: 1.5rem;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            & a {
                // display: block;
                margin-bottom: 0.5rem;
                font-size: 1.25rem;
                .arrow {
                    line-height: 0;
                    top: 50%;
                    font-size: 1.25rem;
                }
            }
        }
        & h1 {
            width: 60%;
            font-family: "Outfit", sans-serif;
            font-style: normal;
            font-weight: 600;
            font-size: 3rem;
            margin: 0;
        }
        &--small {
            width: 20%;
            & .imageHolder {
                height: 60%;
            }
        }
        &--large {
            align-self: flex-end;
            width: 50%;
            align-items: center;
            & h1 {
                min-height: 35%;
                text-align: center;
                // height: 100%;
            }
            & .imageHolder {
                height: 65%;
                // margin-top: 5rem;
            }
            & .links {
                a {
                    margin-left: 2rem;
                    margin-right: 2rem;
                }
                flex-direction: row;
                // justify-content: space-evenly;
            }
        }
    }
}

@keyframes floatingObject {
    0% {
        margin-top: 1%;
    }
    25% {
        margin-top: 2.5%;
    }
    50% {
        margin-top: 4%;
    }
    75% {
        margin-top: 2.5%;
    }
    100% {
        margin-top: 1%;
    }
}