.VideosSlider {
    width: 100%;
    height: calc(100vh - 0rem);
    display: flex;
    flex-direction: column;
    padding-bottom: 2rem;
    background-image: url("/src/assets/images/graphBG.svg");
    background-repeat: no-repeat;
    background-position: center top;
    background-size: 50%;
    &__container {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
        // flex-wrap: nowrap;
        // overflow-x: scroll;
    }
    &__slider {
        width: 100%;
        // height: 100%;
        display: flex;
        align-items: center;
        // flex-wrap: nowrap;
        overflow-x: scroll;
        scroll-behavior: smooth;
        padding: 2rem 0rem;
        /* width */
        &::-webkit-scrollbar {
            height: 8px;
            // width: 5px;
            // height: 5px;
        }
        /* Track */
        &::-webkit-scrollbar-track {
            // box-shadow: inset 0 0 5px grey;
            border-radius: 10px;
        }
        /* Handle */
        &::-webkit-scrollbar-thumb {
            background: $orange;
            border-radius: 8px;
        }
    }
}

.YouTubeVideo {
    &__container {
        margin-right: 5rem;
        &:last-child {
            margin-right: 0rem;
        }
        // max-width: 40%;
        // height: 12.65vw;
        & iframe {
            // width: 100%;
            width: 560px;
            height: 315px;
        }
    }
}

@media only screen and (max-width: 1376px) {
    .YouTubeVideo {
        &__container {
            margin-right: 5rem;
            & iframe {
                // width: 100%;
                width: 445px;
                height: 250px;
            }
        }
    }
}