.Footer {
    // position: ;
    // bottom: 0;
    // left: 0;
    // min-height: 6.5rem;
    // height: 6.5rem;
    background-color: $black;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-bottom: 1.5rem solid $orange;
    &__content {
        width: 90%;
        padding-bottom: 2rem;
        display: flex;
        justify-content: space-between;
    }
    &__navContainer {}
    &__creditsContainer {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-end;
        // padding: 1.2rem 0rem;
        & span {
            // margin-top: 0.5rem;
            font-size: 0.8rem;
        }
        & a {
            padding-top: 0.5rem;
            height: 100%;
            margin-bottom: 1rem;
            &:last-child {
                margin-bottom: 0;
            }
            & img {
                // min-height: 24px;
                height: 70%;
            }
        }
    }
}