.HomePage {
    display: flex;
    justify-content: center;
    &__hero {
        width: 100%;
        height: calc(100vh - 6.5rem);
        display: flex;
        justify-content: space-between;
        & .infoContainer {
            width: 40%;
            max-width: 430px;
            height: 100%;
            padding-top: 5rem;
            & h1 {
                font-family: "Outfit", sans-serif;
                font-style: normal;
                font-weight: 600;
                font-size: 3rem;
                margin-bottom: 0;
            }
            & p {
                font-family: "Outfit", sans-serif;
                font-style: normal;
                font-weight: 400;
                font-size: 1.125rem;
                margin: 1rem 0rem;
            }
            & a {
                margin-top: 1rem;
            }
        }
        & .graphicsContainer {
            padding-top: 7rem;
            width: 60%;
            height: 100%;
            display: flex;
            justify-content: flex-end;
            // align-items: center;
            & img {
                // width: 50%;
                height: fit-content;
                max-height: 320px;
            }
        }
    }
}

.highlight {
    color: $orange;
}