header {
    position: fixed;
    top: 0;
    left: 0;
    min-height: 6.5rem;
    height: 6.5rem;
    background-color: $black;
    z-index: 100;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    & .content {
        width: 90%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
}

.navMenu {
    display: flex;
    list-style: none;
    align-items: center;
    padding: 0;
    & li {
        margin-right: 2rem;
        cursor: pointer;
        position: relative;
        transition: width 0.2s ease-in-out;
        display: flex;
        justify-content: center;
        flex-direction: column;
        font-size: 1.25rem;
        font-weight: 500;
        &:last-child {
            margin-right: 0;
        }
        & a {
            font-family: "Outfit", sans-serif;
            text-decoration: none;
            color: $white;
        }
        &:hover {
            &::after {
                content: "";
                position: absolute;
                width: 100%;
                background-color: $white;
                height: 2px;
                bottom: -0.2rem;
                left: 0%;
                animation: spreadOut 0.2s ease-in-out;
            }
        }
    }
}

@keyframes spreadOut {
    from {
        width: 0%;
        left: 50%;
    }
    to {
        width: 100%;
        left: 0%;
    }
}