.Products {
    width: 100%;
    height: calc(100vh - 0rem);
    display: flex;
    flex-direction: column;
    padding-bottom: 2rem;
    // margin-top: 6.5rem;
    & h2 {
        font-family: "Outfit", sans-serif;
        font-style: normal;
        font-weight: 600;
        font-size: 36px;
        line-height: 45px;
    }
    &__showCase {
        width: 100%;
        height: 100%;
        // background-color: white;
        display: grid;
        grid-template-rows: 1fr 1fr;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 1rem 1rem;
    }
    & .projectCard {
        background-color: $grey;
        border-radius: 0.5rem;
        cursor: pointer;
        transition: all 0.1s ease-in-out;
        &:hover {
            // transform: scale(1.005);
        }
        &__vertical {
            @extend .projectCard;
            grid-column-start: auto;
            grid-column-end: span 1;
            grid-row-start: auto;
            grid-row-end: span 2;
        }
        &__square {
            @extend .projectCard;
            grid-column-start: auto;
            grid-column-end: span 1;
            grid-row-start: auto;
            grid-row-end: span 1;
        }
        &__horizontal {
            @extend .projectCard;
            grid-column-start: auto;
            grid-column-end: span 2;
            grid-row-start: auto;
            grid-row-end: auto;
        }
    }
}